import { useTheme } from "@emotion/react";
import { Box } from "@mui/material";
import React from "react";

const Cta = () => {
  const theme = useTheme();

  return (
    <div>
      <Box
        sx={{
          color: theme.palette.grey.main,
          backgroundColor: theme.palette.primary.main,
        }}
      >
        <div class='max-w-screen-xl px-4 py-8 mx-auto lg:py-16 lg:px-6'>
          <div class='max-w-screen-sm mx-auto text-center'>
            <h2 class='mb-4 text-3xl font-extrabold leading-tight tracking-tight text-gray-900 dark:text-white'>
              Start your free trial today
            </h2>
            <p class='mb-6 font-light text-gray-500 dark:text-gray-400 md:text-lg'>
              Try our service for 30 days. No payment required.
            </p>
            <a
              href='#'
              class='text-white hover:bg-primaryblue bg-green-800 focus:ring-4 focus:ring-purple-300 font-medium rounded-lg text-sm px-5 py-2.5 mr-2 mb-2 dark:bg-purple-600 dark:hover:bg-purple-700 focus:outline-none dark:focus:ring-purple-800'
            >
              Free trial for 30 days
            </a>
          </div>
        </div>
      </Box>
    </div>
  );
};

export default Cta;
